@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap" rel="stylesheet');

.App {
  background-image: url('https://img.freepik.com/free-photo/milky-way-starry-sky-background_34011-265.jpg?size=626&ext=jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  min-height: 100vh;

  color: white;
  font-size: calc(10px + 2vmin);
  font-family: 'Roboto', sans-serif;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center; 
}
