button{
  font-family: 'Roboto', sans-serif;
  font-size: calc(10px + 1vmin);
  color: black;
  transition-duration: 0.4s;
  background: #E4EDEE;
  padding: 15px;
  box-shadow: 0px 5px 5px 0px rgba(0,0,0,0.7);
  border: none;
  border-radius: 5px;
  margin: 20px;
}

button:hover {
  font-weight: bold ;
  background: #B5B8B9;
}

.buttonClick {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center; 

  width: 80%;

  padding: 10px;
}