@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap" rel="stylesheet);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button{
  font-family: 'Roboto', sans-serif;
  font-size: calc(10px + 1vmin);
  color: black;
  transition-duration: 0.4s;
  background: #E4EDEE;
  padding: 15px;
  box-shadow: 0px 5px 5px 0px rgba(0,0,0,0.7);
  border: none;
  border-radius: 5px;
  margin: 20px;
}

button:hover {
  font-weight: bold ;
  background: #B5B8B9;
}

.buttonClick {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center; 

  width: 80%;

  padding: 10px;
}
/*MAP*/
.mapouter{
   position:relative;
   text-align:center;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center; 

  max-width: 90%;

}

.gmap_canvas {
  overflow:hidden;
  background:none!important;
  height:100%;
  width:100%;
}

.no-active{
  display: none;
}
.App {
  background-image: url('https://img.freepik.com/free-photo/milky-way-starry-sky-background_34011-265.jpg?size=626&ext=jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  min-height: 100vh;

  color: white;
  font-size: calc(10px + 2vmin);
  font-family: 'Roboto', sans-serif;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center; 
}

